.wrap {
  width: 1200px !important;
  margin: 0 auto;
}

.common-block {
  padding: 30px 24px;
  border-radius: 15px;
  background-color: #fff;
}

.stake-tabs {
  position: relative;

  .stake-rule {
    position: absolute;
    top: 26px;
    right: 3px;
    font-weight: 300;
    font-size: 12px;
    line-height: 20px;
    color: #fff;
  }
}

.tab-content {
  display: flex;
  justify-content: space-between;

  .tab-title {
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    margin-bottom: 20px;

    > a {
      font-weight: 300;
      font-size: 14px;
      color: #fff;
    }
  }

  .content-l {
    width: calc(100% - 440px);

    .content-l-content {
      border-radius: 15px;
      background-color: #fff;
      overflow: hidden;
    }
  }

  .content-r {
    width: 410px;

    .content-r-content {
      background-color: #fff;
    }
  }
}

.liquidity-ele {
  height: 98px;
  overflow: hidden;
  border-top: 1px solid rgba(229, 229, 229, 0.5);

  > div {
    padding: 30px 24px;
  }

  .cp {
    user-select: none;
  }

  &:first-child {
    padding-top: 0;
    border-top: none;
  }

  &.current {
    height: auto;
    overflow: visible;
  }

  &:last-child {
    padding-bottom: 0;
  }

  > div,
  .liq-ele {
    display: flex;
    justify-content: space-between;
  }

  > div {
    &:first-child {
      padding-top: 30px;
      padding-bottom: 30px;
      border-bottom: 1px solid #e5e5e5;

      &:hover {
        background: #fcfafe;
      }
    }

    &:last-child {
      background: #f6f4fa;
    }
  }

  .liq-logo {
    width: 25%;
    display: flex;
    align-items: center;

    > span {
      display: inline-block;
      font-weight: 500;
      font-size: 16px;
      line-height: 20px;
      color: #333333;

      &:first-child {
        min-width: 44px;
        margin-right: 8px;
      }
    }

    img {
      width: 24px;
      height: 24px;
      float: left;
      margin-left: -5px;

      &:first-child {
        margin-left: 0;
      }
    }

    .pool3-img {
      width: 42px;
      height: 31px;
    }
  }

  .liq-ele {
    width: 70%;

    > div {
      display: flex;
      flex-direction: column;
      width: 25%;
      text-align: right;

      > span {
        display: inline-block;
        line-height: 16px;
        font-weight: 500;
        font-size: 14px;
        color: #333;

        &:first-child {
          font-size: 12px;
          color: #999;
          font-weight: normal;
          margin-bottom: 4px;
        }
      }
    }
  }

  > div:last-child {
    > div {
      width: 49%;
      position: relative;
    }

    .liq-claim {
      min-width: 60px !important;
      height: 30px !important;
      line-height: 30px;
      padding: 0 18px !important;
      border-radius: 5px !important;

      &.btn-disabled {
        background-color: #ccc !important;
      }
    }
  }
}

.liq-slide {
  min-width: 18px;
  margin-left: 24px;
  cursor: pointer;
}

.liq-earned,
.liq-staked {
  padding: 16px 20px;
  border: 1px solid rgba(167, 132, 238, 0.4);
  box-sizing: border-box;
  border-radius: 10px;

  .title {
    line-height: 17px;
    font-size: 12px;
    color: #333;
    margin-bottom: 4px;
  }

  .amount {
    line-height: 17px;
    font-size: 14px;
    color: #333;
    font-weight: bold;
  }
}

.get-lp {
  > span {
    display: inline-block;
    width: 30px;
    height: 30px;
    text-align: center;
    border: 1px solid #5915e1;
    border-radius: 5px;
    font-size: 16px;
    color: #5915e1;
    cursor: pointer;

    &:first-child {
      margin-right: 12px;
    }
  }
}

.sp-title {
  display: flex;
  justify-content: space-between;
  align-self: center;
  position: relative;
  padding-bottom: 16px;
  margin-bottom: 16px;

  &::after {
    content: '';
    display: block;
    height: 1px;
    width: calc(100% + 48px);
    position: absolute;
    left: -24px;
    bottom: 0;
    background: #e5e5e5;
    opacity: 0.5;
  }

  > div {
    &:first-child {
      display: flex;
      justify-content: space-between;
      align-self: center;

      > img {
        width: 26px;
        height: 26px;
      }

      > span {
        font-weight: 500;
        font-size: 18px;
        line-height: 25px;
        color: #333333;
        margin-left: 8px;
      }
    }

    &:nth-child(2) {
      text-align: right;

      > div {
        font-size: 12px;
        line-height: 17px;
        color: #999999;

        &:nth-child(2) {
          font-weight: bold;
          font-size: 20px;
          line-height: 24px;
          color: #5915e1;
        }
      }
    }
  }

  > button {
    min-width: 80px !important;
    height: 40px !important;
    line-height: 40px;
    font-weight: 600 !important;
    font-size: 14px !important;
    padding: 0 !important;
  }
}

.sp-content {
  padding: 14px 16px;
  background: #f5f6f9;
  border-radius: 5px;
  margin-bottom: 12px;

  &:last-child {
    margin-bottom: 0;
  }

  > p {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    line-height: 17px;
    color: #333;
    margin-bottom: 12px !important;

    &:last-child {
      margin-bottom: 0 !important;
    }

    &.title {
      line-height: 20px;

      > span {
        font-weight: 500;
        font-size: 14px;
        color: #333333;
      }
    }

    > span {
      font-size: 12px;

      > em {
        color: #5915e1;
        cursor: pointer;
      }
    }
  }

  &.stake-sun,
  &.lock-sun {
    display: flex;
    justify-content: space-between;
    align-items: stretch;

    > div {
      width: 31%;
      height: 100%;
      text-align: right;

      &:first-child {
        min-width: 74px;
        width: 32%;
        height: auto;
        text-align: left;
        padding-top: 16px;

        > img {
          width: 28px;
          height: 28px;
        }

        > span {
          font-weight: 500;
          font-size: 14px;
          line-height: 20px;

          color: #333333;
        }
      }

      &:nth-child(2) {
        min-width: 90px;

        > p {
          &:last-child {
            margin-top: 8px;
          }
        }
      }

      &:last-child {
        width: 38%;
        margin-left: 15px;
      }

      > p {
        min-height: 25px;

        img {
          width: 12px;
          margin-top: -2px;
        }

        &:first-child {
          font-size: 12px;
          line-height: 17px;
          color: #999999;
        }

        &:last-child {
          font-weight: bold;
          font-size: 16px;
          line-height: 20px;
          color: #5915e1;
        }
      }
    }
  }
}

.stake-lock-btn {
  min-width: 60px !important;
  height: 30px !important;
  line-height: 30px;
  font-weight: 500 !important;
  font-size: 12px !important;
  text-align: center;
  border: 1px solid #5915e1 !important;
  border-radius: 5px !important;
  color: #5915e1 !important;
  background: transparent !important;
  padding: 0 !important;
  margin-top: 4px;
}

.sp-btns {
  display: flex;
  justify-content: space-between;

  > button {
    width: 49%;
  }
}

.sp-reward {
  > button {
    width: 100%;
    height: 40px !important;
    line-height: 40px;
    font-weight: 600 !important;
    font-size: 14px !important;
    margin-bottom: 12px;
    padding: 0 !important;

    &:last-child {
      border: 1px solid #5915e1 !important;
      color: #5915e1 !important;
      background: #fff !important;
    }
  }
}

.lc-info {
  margin-top: 8px;
  padding: 18px 16px;
  background: #eee8fc;
  border-radius: 5px;

  > p {
    display: flex;
    justify-content: space-between;
    margin: 0 0 12px 0;

    &:last-child {
      margin: 0;
    }

    > span {
      font-size: 12px;
      color: #5915e1;

      .opa-5 {
        opacity: 0.5;
      }

      &.opa-5 {
        opacity: 0.5;
      }

      &:last-child {
        font-weight: 500;
        font-size: 14px;
        color: #5915e1;
        opacity: 1;
      }
    }
  }
}

.lc-date {
  .lc-date-eles {
    display: flex;
    flex-flow: wrap;

    > span {
      display: inline-block;
      min-width: 50px;
      padding: 0 5px;
      line-height: 30px;
      text-align: center;
      border: 1px solid #e1e1e1;
      margin: 0 10px 10px 0;
      border-radius: 5px;
      font-size: 12px;
      color: #333;
      cursor: pointer;

      &.current {
        border-color: #5915e1;
        color: #5915e1;
      }
    }
  }
}

.max-btn {
  display: inline-block;
  cursor: pointer;
}

.stake-tabs {
  .ant-tabs-nav {
    background: transparent;
    padding: 7px 0 !important;
    margin-bottom: 14px !important;

    &::before {
      border-bottom: 1px solid rgba($color: #ebe5f0, $alpha: 0.4) !important;
      bottom: 7px !important;
    }

    .ant-tabs-tab {
      .ant-tabs-tab-btn {
        line-height: 25px;
        font-weight: 600;
        font-size: 16px;
        color: rgba(255, 255, 255, 0.4);
      }

      &.ant-tabs-tab-active {
        .ant-tabs-tab-btn {
          color: #fff;
          font-size: 18px;
        }
      }
    }

    .ant-tabs-ink-bar {
      height: 1px !important;
      background: #ffffff;
      border-radius: 10px;
    }
  }
}

.sp-rule {
  .sp-title {
    margin-bottom: 0;
    font-weight: 500;
    font-size: 18px;
    color: #333;
  }

  > ul > li {
    overflow: hidden;
    padding: 12px 0;
    border-bottom: 1px solid rgba(229, 229, 229, 0.5);
    cursor: pointer;

    > p {
      line-height: 24px;
      font-size: 14px;
      color: #333;
      word-break: break-word;

      &:first-child {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0 !important;
      }

      &:last-child {
        display: none;
        line-height: 22px;
        font-size: 12px;
        color: #999;
        margin-top: 10px;
      }
    }

    .slide-icon {
      display: inline-block;
      width: 14px;
      height: 14px;
      background: url(../images/slideUpLight.svg) 0 0 no-repeat;
      background-size: 100%;
      transform: rotate(180deg);
    }

    &.current {
      &:first-child {
        margin-bottom: 12px !important;
      }

      > p:last-child {
        display: block;
      }

      .slide-icon {
        transform: rotate(0deg);
      }
    }
  }
}

.line {
  width: 100%;
  height: 1px;
  background: #e5e5e5;
  opacity: 0.5;
  margin: 16px 0;
}

button {
  text-align: center;
  border-radius: 5px !important;
}

.vertical-center-modal {
  display: flex;
  align-items: center;
  justify-content: center;
}

.vertical-center-modal .ant-modal {
  top: 50px;
}

.lock-modal {
  &.detail-modal {
    .ant-modal-body {
      padding-top: 0;
    }

    &.top-modal {
      margin-top: -200px;

      .ant-modal-body {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 150px;
      }

      .ant-statistic-content-value {
        color: rgba(0, 0, 0, 0.45);
        font-size: 14px;
      }

      .ant-statistic-content-suffix {
        color: rgba(0, 0, 0, 0.45);
        font-size: 14px;
      }
    }
  }

  .ant-modal-header {
    border-bottom: none;
  }

  .ant-modal-title {
    font-weight: 500;
    font-size: 14px;
    color: #333333;
    text-align: center;
    margin-top: 14px;
  }

  .ant-modal-content {
    border-radius: 15px;
  }

  .ant-modal-header {
    border-radius: 15px 15px 0 0;
  }

  .ant-modal-body {
    padding-top: 14px;
  }

  .ant-tabs-nav-wrap {
    justify-content: center;

    .ant-tabs-tab {
      min-width: 100px;
      justify-content: center;

      .ant-tabs-tab-btn > span {
        font-weight: 500;
        font-size: 14px;
        color: #999;
        text-shadow: none !important;
      }

      &.ant-tabs-tab-active {
        .ant-tabs-tab-btn > span {
          color: #5915e1;
        }
      }
    }
  }

  .ant-tabs-content-holder {
    margin-top: 10px;
  }

  .ant-tabs-ink-bar {
    height: 2px;
    background: #5915e1;
    border-radius: 20px;
  }
}

.common-btn {
  width: 100%;
  height: 40px;
  line-height: 40px;
  font-weight: 600;
  font-size: 14px;
  letter-spacing: -0.3px;
  color: #fff;
  text-align: center;
  background: #5915e1;
  border-radius: 5px !important;
  padding: 0;
  border: none;
  box-shadow: none;
  cursor: pointer;
  margin-top: 20px;

  &:disabled {
    color: #b79cef !important;
    background: rgba(89, 21, 225, 0.1) !important;
    cursor: not-allowed !important;
  }

  &.mt-ers {
    margin-top: 60px;
  }

  &.border {
    border: 1px solid #5915e1 !important;
    color: #5915e1 !important;
    background: #fff !important;

    &:disabled {
      color: #b79cef !important;
      cursor: not-allowed !important;
      border-color: #b79cef !important;
    }
  }
}

.common-error {
  height: 32px;
  line-height: 32px;
  padding: 0 10px;
  margin-top: 8px;
  background: #f8f7c8;
  color: #333;
  text-align: left;
  border-radius: 5px;
  font-size: 12px;
}

.jcfe {
  justify-content: flex-end !important;
}

.ant-input-affix-wrapper {
  height: 40px;
  line-height: 40px;
  border: 1px solid #5915e1;
  box-sizing: border-box;
  border-radius: 5px;
  padding: 4px 16px;

  .max-btn {
    min-width: 48px;
    height: 24px;
    line-height: 24px;
    background: #5915e1;
    border-radius: 5px;
    font-size: 12px;
    color: #fff;
    text-align: center;
  }
}

.vest-table {
  th {
    font-size: 14px !important;
    color: #999 !important;
    font-weight: normal !important;
    background-color: #fff !important;
    padding-top: 0 !important;
    padding-bottom: 12px !important;
  }

  td {
    color: #333;
    border: none !important;
  }

  th,
  td {
    line-height: 20px;
    width: 33.33%;
    padding: 8px 6px !important;
    font-size: 12px;
  }

  tr {
    &:first-child {
      border-top: 1px solid rgba(229, 229, 229, 0.5) !important;
    }
  }
}

.lock-table {
  th,
  td {
    width: 50%;
  }
}

.ant-tabs-ink-bar-animated {
  transition: none !important;
}

.stake-tab {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .v-icon {
    display: inline-block;
    width: 21px;
    height: 23px;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 0 0;
    margin-right: 8px;
  }

  &.stake-tab1 {
    .v-icon {
      background-image: url(../images/v1_icon.svg);
    }
