.ant-tabs-tab-active {
  .stake-tab1 {
    .v-icon {
      background-image: url(../images/v1_active_icon.svg);
    }
  }

  .stake-tab2 {
    .v-icon {
      background-image: url(../images/v2_active_icon.svg);
    }
  }
}

