.new-lock-modal {
  .ant-modal-header {
    padding: 27px 24px;
    background-image: url(../images/stake_new_lock_head.png);
    background-size: cover;
    background-repeat: no-repeat;
  }

  .ant-modal-title {
    text-align: left;
    font-weight: 500;
    font-size: 18px;
    line-height: 20px;
    color: #333333;
    margin: 0;
    margin-right: 30px;
  }

  .ant-input-affix-wrapper {
    padding: 4px 10px;
  }

  .ant-picker {
    background-color: #f5f6f9;
    border-radius: 5px;
    border: none;
  }

  .ant-picker-suffix img {
    vertical-align: middle;
  }

  .line {
    &:first-child {
      margin-bottom: 10px;
    }
  }

  .lc-info {
    background: #f9f9f9;
  }

  .lock-common-btn:disabled {
    color: #fff !important;
    background: #cacacc !important;
  }

  .lc-date {
    .lc-date-eles {
      > span {
        padding: 0 5px;
        margin: 0 2.6% 10px 0;
        width: 23%;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    .error {
      font-weight: 400;
      font-size: 12px;
      line-height: 17px;
      color: #ed635a;
      height: 17px;
    }
  }
}

.calculator-modal {
  .ant-modal-body {
    padding-top: 6px;
  }

  .ant-modal-header {
    padding: 25px 30px;
    background-image: url(../images/stake_calculator_head.png);
    background-size: cover;
    background-repeat: no-repeat;
  }

  .ant-modal-title {
    text-align: left;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: #333333;
    margin: 0;
  }

  .ant-select:not(.ant-select-customize-input) .ant-select-selector {
    background-color: #f5f6f9;
    border-radius: 5px;
    height: 30px;
    width: 142px;
  }

  .ant-input-affix-wrapper {
    padding: 4px 10px;
  }

  .gray {
    margin-top: 12px;
  }

  section {
    border-bottom: 1px solid rgba(229, 229, 229, 0.5);
    padding: 10px 0 10px 0;

    &:nth-child(n + 4) {
      padding: 20px 0 20px 0;
    }

    &:last-child {
      padding: 20px 0 0 0;
      border: none;
    }
  }

  .number {
    max-width: 50%;
  }
}

.tips-modal {
  .ant-modal-header {
    padding: 30px 0;
  }

  .ant-modal-title {
    margin: 0;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: #333333;
  }

  .ant-modal-body {
    padding: 0 30px 30px;
  }

  .step {
    border-radius: 15px;
    font-size: 14px;
    line-height: 20px;
    color: #333333;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    padding: 40px 130px 20px 30px;

    &:last-child {
      margin-bottom: 0px;
    }

    .ant-btn {
      border-radius: 3px;
      padding: 5px 16px;
      font-size: 12px;
      line-height: 17px;
      // width: 80px;
      height: 26px;
      border: none;
      margin-top: 12px;
    }

    .icon {
      width: 76px;
      height: 24px;
      position: absolute;
      top: 0;
      left: 0;
      text-align: center;
      line-height: 24px;
      font-weight: 500;
      font-size: 12px;
      background-size: cover;
      background-repeat: no-repeat;
    }
  }

  .step_1 {
    background-color: #fffaed;
    background-image: url(../images/stake_tips_step_bg1.png);

    .ant-btn {
      color: #b98500;
      background: linear-gradient(274.02deg, rgba(255, 167, 36, 0.5) -43.5%, rgba(243, 225, 62, 0.5) 131.93%);
      border: none;
    }

    .icon {
      color: #e7af25;
      background-image: url(../images/stake_tips_step1.png);
    }
  }

  .step_2 {
    background-color: #fff6ff;
    background-image: url(../images/stake_tips_step_bg2.png);

    .ant-btn {
      color: #a021d7;
      background: linear-gradient(133.67deg, rgba(239, 96, 250, 0.4) -7.09%, rgba(146, 93, 255, 0.4) 140.69%);
    }

    .icon {
      color: #c057d2;
      background-image: url(../images/stake_tips_step2.png);
    }
  }

  .step_3 {
    background-color: #f1f3ff;
    background-image: url(../images/stake_tips_step_bg3.png);

    .ant-btn {
      color: #ba133b;
      background: linear-gradient(323.52deg, rgba(255, 72, 190, 0.3) 2.44%, rgba(255, 71, 115, 0.3) 154.47%);
    }

    .icon {
      color: #8a88ff;
      background-image: url(../images/stake_tips_step3.png);
    }
  }
}

.v3-stake-token {
  .common-btn:disabled {
    color: #fff !important;
    background: #cacacc !important;
  }

  .ant-modal-header {
    padding: 27px 30px;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .ant-modal-title {
    text-align: left;
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    margin: 0;
  }

  .ant-input-affix-wrapper {
    padding: 4px 10px;
  }
}

.v3-thrpoollp {
  .ant-modal-header {
    background-image: url(../images/stake_3pool_head.png);
  }
}

.v3-sunlp {
  .ant-modal-header {
    background-image: url(../images/stake_sun_trx_head.png);
  }
}

.v3-thrsunlp {
  .ant-modal-header {
    background-image: url(../images/stake_usdc_head.png);
  }
}

.sun-apply {
  display: flex;
  justify-content: space-between;
  padding: 20px 30px;
  background: #fff;
  border-radius: 10px;
  font-size: 14px;
  color: #000;
  .sa-title {
    position: relative;
    > img {
      position: absolute;
      top: 4px;
      width: 14px;
      height: 14px;
      margin-right: 4px;
    }
    > span {
      display: inline-block;
      padding-left: 18px;
    }
  }
  a {
    display: inline-block;
    font-size: 14px;
    color: #7534f9;
    margin: 0 4px;
    &:hover {
      text-decoration: underline;
    }
  }
}

.more-pool {
  display: flex;
  justify-content: space-between;
  padding: 11px 60px 11px 30px;
  background: #fff;
  border-radius: 10px;
  font-size: 14px;
  color: #000;
  height: 50px;
  line-height: 32px;
  margin-bottom: 20px;
  cursor: pointer;
}

@media screen and (max-width: 1300px) {
  .wrap,
  .banner.pool-config {
    width: 90% !important;
  }
}

@media screen and (max-width: 1100px) {
  .tab-content {
    display: block;

    .content-l,
    .content-r {
      width: 100%;
    }

    .content-l {
      margin-bottom: 20px;
    }
  }
}

@media screen and (max-width: 800px) {
  .sun-apply {
    display: block;
    margin-bottom: 30px;
    .sa-title {
      margin-bottom: 10px;
    }
  }
  .wrap-mobile {
    width: calc(100% - 30px) !important;
  }
}

@media screen and (max-width: 600px) {
  .lock-modal {
    top: 60px;
  }

  .liq-earned .title,
  .liq-staked .title {
    display: flex;
    justify-content: space-between;
  }

  .liquidity-ele {
    > div {
      &:nth-child(2) {
        display: block;

        > div {
          width: 100%;

          &:first-child {
            margin-bottom: 10px;
          }
        }
      }
    }

    .liq-logo {
      img {
        width: 20px;
        height: 20px;
      }

      > span {
        font-size: 14px;

        &:first-child {
          display: none;
        }
      }
    }

    .liq-ele {
      display: block;

      > div {
        width: 48%;
        flex-direction: row;
        float: left;
        justify-content: space-between;

        &:nth-child(2n) {
          margin-left: 4%;
        }

        > span {
          font-size: 12px;
        }
      }
    }
  }

  .wrap-mobile {
    .liquidity-ele {
      .liq-logo {
        width: 20%;
      }

      .liq-ele {
        width: 70%;

        > div {
          // width: 100%;
          line-height: 20px;
        }
      }

      .liq-slide {
        width: 5%;
      }
    }

    .stake-tabs .stake-rule {
      font-size: 12px;
      top: 22px;
      right: 0;
    }

    .ant-tabs-tab-btn {
      font-size: 12px !important;
    }

    .ant-tabs-tab {
      margin-left: 8px !important; // ??
    }

    .stake-tabs .ant-tabs-tab {
      &:first-child {
        margin-left: 0 !important;
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .vertical-center-modal .ant-modal {
    top: 0;
  }
  .sp-content.stake-sun > div:first-child,
  .sp-content.lock-sun > div:first-child {
    > img {
      display: block;
    }
  }

  .wrap-mobile {
    .liquidity-ele {
      height: 140px;

      &:first-child {
        padding-top: 0;
      }

      &.current {
        height: auto;
      }

      .liq-logo {
        width: 35%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: flex-start;

        > span:first-child {
          display: block;
          margin-bottom: 5px;
        }
      }

      .liq-ele {
        width: 65%;

        > div {
          width: 100%;
          margin-left: 0;
        }
      }
    }

    .sp-title > button {
      min-width: 64px !important;
      height: 36px !important;
      line-height: 36px;
      font-size: 12px !important;
    }

    .sp-reward > button {
      margin: 10px auto;
    }
  }

  .common-block {
    padding: 30px 15px;
  }

  .new-lock-modal,
  .calculator-modal,
  .v3-stake-token {
    .ant-modal-header {
      padding: 10px 24px;
    }

    .ant-modal-title {
      font-size: 16px;
    }

    .ant-modal-close-x {
      width: 40px;
      height: 40px;
      line-height: 40px;
      margin-right: 10px;
    }
  }

  .tips-modal {
    .step {
      height: 150px;
      padding: 35px 90px 20px 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;

      &.step_1 {
        background-image: url(../images/stake_tips_step_bg_m1.png);
      }

      &.step_2 {
        padding: 30px 93px 20px 20px;
        background-image: url(../images/stake_tips_step_bg_m2.png);
      }

      &.step_3 {
        background-image: url(../images/stake_tips_step_bg_m3.png);
      }

      .ant-btn {
        margin-top: 5px;
      }
    }
  }
}

@media screen and (max-width: 450px) {
  .wrap-mobile .stake-tabs .stake-rule {
    top: 60px;
  }
}

@media screen and (max-width: 400px) {
  .wrap-mobile .stake-tabs .stake-rule {
    // top: 52px; // ??
  }

  .header-toast .toast-text {
    margin-left: -5px;
  }
}

@media screen and (max-height: 666px) {
  .calculator-modal {
    transform: scale(0.9);
  }
}
