    overflow: hidden;
  }
  .new-content {
    background-color: #fff !important;
    padding: 30px;

    > div {
      width: 49%;
    }
  }
  .liquidity-ele {
    position: relative;
    overflow: hidden;
    height: 204px;
    margin-bottom: 30px;
    border-radius: 15px;
    &.current {
      height: auto !important;
      .liq-earned-staked {
        position: relative;
        &::before {
          content: '';
          display: inline-block;
          width: 0;
          height: 0;
          border: 9px solid transparent;
          border-bottom: 9px solid #f6f4fa;
          position: absolute;
          top: -18px;
          left: 60px;
        }
      }
    }
    > div {
      &:last-child {
        // background-color: #fff;
        > div {
          width: 48.6%;
        }
      }
    }
    .liq-earned-staked {
      padding: 30px;
    }
    .liq-title {
      background-color: #fff;
      padding: 25px 40px;
      border-bottom: 0;
      position: relative;
      align-items: center;
      &:hover {
        background-color: #fff;
      }
      &::after {
        content: '';
        display: block;
        width: calc(100% - 60px);
        height: 1px;
        background-color: #e5e5e5;
        position: absolute;
        bottom: 0;
        left: 30px;
      }
      &.speed-up {
        background: url(../images/liq-title-bg.png) #fff 0 0 repeat;
        animation: bgMove 30s linear infinite;
        &::after {
          display: none;
        }
      }
    }
    .speed-icon {
      position: absolute;
      top: 0;
      left: 0;
      display: inline-block;
      width: 100px;
      height: 30px;
      line-height: 30px;
      padding: 0;
      text-align: center;
      background-color: #ae6def;
      border-radius: 0 0 25px 0;
    }
    .liq-info {
      background-color: #fff;
      padding: 30px;
    }
    .liq-ele {
      width: calc(100% - 110px);
      > div {
        width: 18%;
        text-align: left;

        &:nth-child(1) {
          width: 24%;
        }
        &:nth-child(2) {
          width: 26%;
        }
        &:nth-child(3) {
          width: 28%;
        }
        &:nth-child(4) {
          width: 17%;
        }
        &:nth-child(5) {
          text-align: right;
          width: 12%;
          white-space: nowrap;
        }
        > span {
          &:first-child {
            font-size: 14px;
            font-weight: normal;
            margin-bottom: 10px;
            white-space: nowrap;
          }
          &:last-child {
            font-size: 16px;
            font-weight: 500;
          }
        }
      }
    }
    .liq-logo {
      min-width: 240px;
      width: 44%;
      align-items: center;
      flex-grow: 2;
      > span {
        &:first-child {
          min-width: 72px;
          margin-right: 10px;
        }
      }
      img {
        width: 40px;
        height: 40px;
        float: none;
        margin-left: -7px;
        &:first-child {
          margin-left: 0;
        }
      }
      .tooltip-img {
        width: 18px;
        height: 18px;
      }
      .pool3-img {
        width: 72px !important;
        height: 53px !important;
      }
      .pool2-img {
        width: 80px;
        height: 43px;
      }
      .earn-title {
        font-weight: bold;
        font-size: 20px;
        line-height: 29px;
        margin-bottom: 5px;
      }
      .earn-subtitle {
        font-size: 14px;
        line-height: 16px;
        opacity: 0.7;
        font-weight: normal;
      }
      .earn-title,
      .earn-subtitle {
        display: flex;
        align-items: flex-start;
        color: #666;
        white-space: nowrap;
      }
      .liq-version {
        display: inline-block;
        width: 42px;
        height: 20px;
        line-height: 20px;
        margin-left: 10px;
        border-radius: 10px;
        font-size: 12px;
        font-weight: normal;
        text-align: center;
        &.v1 {
          color: #715fdf;
          background: rgba(113, 95, 223, 0.1);
        }
        &.v2 {
          color: #e44646;
          background: rgba(228, 70, 70, 0.1);
        }
      }
    }
  }
  .liq-ele-new {
    border-top: none;
    background-color: #fff;
    .liq-info {
      padding: 30px 45px;
    }
  }
  .lock-ele {
    height: auto;
