.speed-basic {
  display: flex;
  align-items: center;
  // justify-content: center;
  min-width: 198px;
  height: 44px;
  line-height: 44px;
  padding: 0 20px 0 26px;
  background-color: #fafafc;
  font-size: 14px;
  font-weight: 500;
  color: #333;
  text-align: center;
  border-radius: 10px;
  white-space: nowrap;
  &.speed-lock {
    background: url(../images/speed-lock-bg.png) 0 0 no-repeat;
    background-size: 100%;
    padding-right: 40px;
    cursor: pointer;
  }
  &.speed-fly {
    background: url(../images/speed-fly-bg.png) 0 0 no-repeat;
    background-size: 100%;
    padding-right: 22px;
    color: #fff;
    position: relative;
    cursor: pointer;
    &::before,
    &::after {
      content: '';
      display: block;
      width: 27px;
      height: 35px;
      position: absolute;
      top: -11px;
      right: 11px;
      background-position: 0 0;
      background-repeat: no-repeat;
      background-size: 100%;
    }
    &::before {
      background-image: url(../images/plane.png);
    }
    &::after {
      width: 55px;
      height: 43px;
      background-image: url(../images/cloud.png);
      top: unset;
      bottom: -22px;
      right: 0;
    }
    &.active {
      &::before {
        animation: fly 0.8s ease-in forwards;
      }
      &::after {
        animation: hide 0.5s ease-in forwards;
      }
    }
  }
}

