@keyframes fly {
  0% {
    top: -11px;
  }
  100% {
    top: -266px;
  }
}

@keyframes hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes bgMove {
  0% {
    background-position: 1200% 0;
  }
  100% {
    background-position: 0 0;
  }
}

@keyframes opacityAni {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

